export default {
  name: 'locale-changer',

  data() {
    return {
      langs: [{
        value: 'zh_CN',
        label: "简体中文"
      }, {
        value: 'en_US',
        label: "English"
      }]
    };
  },

  methods: {
    langChanged(lang) {
      console.log("langChanged=>", lang);
      localStorage.Lang = lang;
    }

  },

  mounted() {
    if (localStorage.Lang != null) this.$i18n.locale = localStorage.Lang;
  }

};